<script>
	import { onMount } from 'svelte';
	
	import Navbar from './components/Navbar.svelte';
	import Menu from './components/Menu.svelte';
	import News from './components/News.svelte';
	import Footer from './components/Footer.svelte';
	import Main from './pages/Main.svelte';
	import Task1 from './pages/Task1.svelte';
	import Task2 from './pages/Task2.svelte';
	import Task3 from './pages/Task3.svelte';

	onMount(() => {
		getRouter();
	});

	let elem = null;
	let instance = null;

	let currentRouter = 'main';
	let pageHeader = getPageHeader();
	let urlParams = new URLSearchParams(window.location.search);

	document.addEventListener('DOMContentLoaded', function() {
		elem = document.querySelector('.sidenav');
		instance = M.Sidenav.init(elem);
	});

	function getRouter() {
		if (urlParams.has('route')) {
			let route = urlParams.get('route');
			currentRouter = route;
			setPageHeader(route);
		}
	}

	function setRouter(router) {
		instance.close();
		updateQueryState(router);
		currentRouter = router;
		setPageHeader(router);
	}

	function updateQueryState(router) {
		urlParams.set('route', router);
		let newRelativePathQuery = window.location.pathname + '?' + urlParams.toString();
		history.pushState(null, '', newRelativePathQuery);
	}

	function getPageHeader(router = currentRouter) {
		let routerLabels = {
			main: '–ì–ª–∞–≤–Ω–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞',
			task1: '–ó–∞–¥–∞–Ω–∏–µ 1',
			task2: '–ó–∞–¥–∞–Ω–∏–µ 2',
			task3: '–ó–∞–¥–∞–Ω–∏–µ 3'
		}

		return routerLabels[router];
	}

	function setPageHeader(router = currentRouter) {
		pageHeader = getPageHeader(router);
	}

	function handleRouteFromEvent(event) {
		setRouter(event.detail);
	}
</script>

<div class="wrapper">
	<Navbar on:handleRoute={handleRouteFromEvent} />
	<Menu on:handleRoute={handleRouteFromEvent} />
	<div class="container page-wrapper">
		<div class="row">
			<div class="col s12 m8">
			<h4 class="page-header">{pageHeader}</h4>
			{#if currentRouter === 'main'}
				<Main on:handleRoute={handleRouteFromEvent} />
			{:else if currentRouter === 'task1'}
				<Task1 />
			{:else if currentRouter === 'task2'}
				<Task2 />
			{:else if currentRouter === 'task3'}
				<Task3 />
			{:else}
				<p>Router not found</p>
			{/if}
			</div>
			<div class="col s12 m4">
				<News />
			</div>
		</div>
	</div>

	<Footer on:handleRoute={handleRouteFromEvent} />
</div>

