<script>
    import { fade } from 'svelte/transition';
    
    let minYear = 1900;
    let maxYear = 1999;

    let year = 0;
    let month = 0;
    let day = 0;

    let randomDate = {};

    const monthDays = {
        0: 31,
        1: isLeapYear() ? 29 : 28,
        2: 31,
        3: 30,
        4: 30,
        5: 31,
        6: 30,
        7: 31,
        8: 30,
        9: 31,
        10: 30,
        11: 31,
    }

    const monthLabels = {
        0: '—è–Ω–≤–∞—Ä—è',
        1: '—Ñ–µ–≤—Ä–∞–ª—è',
        2: '–º–∞—Ä—Ç–∞',
        3: '–∞–ø—Ä–µ–ª—è',
        4: '–º–∞—è',
        5: '–∏—é–Ω—è',
        6: '–∏—é–ª—è',
        7: '–∞–≤–≥—É—Å—Ç–∞',
        8: '—Å–µ–Ω—Ç—è–±—Ä—è',
        9: '–æ–∫—Ç—è–±—Ä—è',
        10: '–Ω–æ–±—è—Ä—è',
        11: '–¥–µ–∫–∞–±—Ä—è',
    }

    const weekLabels = {
        0: '–ø–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫',
        1: '–≤—Ç–æ—Ä–Ω–∏–∫',
        2: '—Å—Ä–µ–¥–∞',
        3: '—á–µ—Ç–≤–µ—Ä–≥',
        4: '–ø—è—Ç–Ω–∏—Ü–∞',
        5: '—Å—É–±–±–æ—Ç–∞',
        6: '–≤–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ'
    }

    function generateDateTime() {
        year = getRandomYear();
        month = getRandomMoth();
        day = generateRandomDayByMonth(month);

        return new Date(year, month, day);
    }

    function getRandomYear() {
        return (Math.random() * (maxYear - minYear) + minYear).toFixed(0);
    }

    function getRandomMoth() {
        return (Math.random() * (11 - 0) + 0).toFixed(0);
    }

    // get max of month from monthDays
    function generateRandomDayByMonth(month) {
        return (Math.random() * (monthDays[month] - 1) + 1).toFixed(0);
    }

    // check if year is leap
    function isLeapYear() {
        return ((year % 4 == 0) && (year % 100 != 0)) || (year % 400 == 0);
    }

    function initDate() {
        let generatedDateTime = generateDateTime();

        randomDate = {
            year: generatedDateTime.getFullYear(),
            month: monthLabels[generatedDateTime.getMonth()],
            day: generatedDateTime.getDate(),
            week: weekLabels[generatedDateTime.getDay()],
        }
    }

    initDate();
</script>

<div class="row" in:fade="{{delay: 50, duration: 300}}">
    <div class="col s12 m12">
        <div class="card">
            <div class="card-content">
                <div class="card-header">–í–∞—Ä–∏–∞–Ω—Ç 24, –∑–∞–¥–∞–Ω–∏–µ 1</div>
                <div class="task">
                    <p>–ó–∞–¥–∞–Ω–∏–µ:</p>
                    <blockquote>
                    –°–æ–∑–¥–∞—Ç—å HTML-—Å—Ç—Ä–∞–Ω–∏—Ü—É, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å–ª—É—á–∞–π–Ω—ã–º –æ–±—Ä–∞–∑–æ–º —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç –≥–æ–¥ 
                    (–≤ –ø—Ä–µ–¥–µ–ª–∞—Ö 1900 –∏ 1999), –º–µ—Å—è—Ü –∏ —á–∏—Å–ª–æ –∏ –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç, 
                    –∫–∞–∫–∏–º –¥–Ω–µ–º –Ω–µ–¥–µ–ª–∏ –æ–∫–∞–∑–∞–ª–∞—Å—å —ç—Ç–∞ –¥–∞—Ç–∞.
                    </blockquote>
                </div>
                <div class="answer">
                    <p>–†–µ—à–µ–Ω–∏–µ:</p>
                    <div class="container">
                        <div class="row">
                            <div class="col s6">
                                <input bind:value={minYear} type="number">
                                <span>–º–∏–Ω–∏–º–∞–ª—å–Ω–∞—è –¥–∞—Ç–∞</span>
                            </div>
                            <div class="col s6">
                              <input bind:value={maxYear} type="number">
                              <span>–º–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è –¥–∞—Ç–∞</span>
                            </div>
                        </div>
                    </div>
                    <div>–°–ª—É—á–∞–π–Ω–æ —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –¥–∞—Ç–∞: 
                        <span>{randomDate.day} {randomDate.month} {randomDate.year}, {randomDate.week}</span>
                    </div>
                    <br>
                    <a href="#!" class="waves-effect waves-light btn" on:click="{initDate}">–°–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å —Å–Ω–æ–≤–∞</a>
        
                </div>
            </div>
        </div>
    </div>
</div>
         